Vue.use(Vuex);

//vuex:,,
var store = new Vuex.Store({
    state:{   //
        carCount:0,  //
        isLogin:false //
    },        
    mutations:{   //
      increment(state,count){
          state.carCount+=count;
      },
      substract(state,count){
        state.carCount-=count;
        if(state.carCount<=0){
            state.carCount=0;
        }
      },
      clearcount(state){
        state.carCount=0;
      }
    },    
    getters:{ //
        optCount:function(state){
            return state.carCount
        }
    }       
});

