<template>
  <!--  -->
  <div>
  <div class="app-goods-info">
    <div class="mui-card">
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <mt-swipe :auto="3000" style="height:200px;">
      <mt-swipe-item v-for="(item,i) in imgList" :key="item.id">
        <img :src="item.md" alt="" style="width:100%">
      </mt-swipe-item>
    </mt-swipe>
        </div>
      </div>
    </div>
    <!--  -->
     
   		<div class="mui-card">
				<div class="mui-card-header">{{products.title}}</div>
				<div class="mui-card-content">
					<div class="mui-card-content-inner">
            <div class="details">{{products.title}}</div>
            <div class="price">
            <b>: </b>
            <span class="old">{{(Number(products.price)).toFixed(2)}}</span>
            <span class="now">:{{(Number(products.price)).toFixed(2)}}</span>
           </div>
          <div class="count">
            <h6></h6>
            <mt-button size="small" @click="goSub()">-</mt-button>
            <span class="number">{{count}}</span>
            <mt-button size="small"  @click="goAdd()">+</mt-button>
         
            <p class="booking">
              <mt-button size="small" type="primary"></mt-button>
              <mt-button size="small" type="default" :class="{love_active:iscollection}" @click="addCart()"></mt-button>
            </p>
          </div>
					</div>
				</div>
			</div>
    <!--  -->
    <div class="mui-card">
      <div class="mui-card-header"></div>
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
           <div>
            <ul class="info-list">
              <li>:{{products.product_number}}</li>
             <li>:{{products.subtitle}}</li>
              <li>:{{products.promise}}</li>
              <li>:{{products.prompt}}</li> 
            </ul>
          </div>
        </div>
        <div class="mui-card-footer">
        <div class="intro">
          <p> 
            <mt-button size="small" @click="show_lgImg()"></mt-button>
          </p>
          <p>
            <mt-button size="small"></mt-button>
           </p>
        </div>
        </div>
         <div class="intro-img" v-if="ifshow_lgImg">
          <img :src="products.lg_pic" alt="">
        </div>
      </div>
    </div>
  </div>
  </div>
</template>
<script>
import swiper from "../subcomponents/swiper";
import Qs from "qs";
import {Toast} from "mint-ui";
// mui'.js 
// import mui from "../../lib/mui/js/mui.js";
export default {
  inject: ['reload'],
  data() {
    return {
      count:1,
      tid: this.$route.params.id,
      list: [],
      md:[],
      imgList:[],
      products:[],
      ifshow_lgImg:false,
      islogin:false,
      uname:"",//
      uid:"",//uid
      iscollection: false,
    };
  },
  methods: {
  
    addCart(){
      var tid=this.products.tid;
      var title=this.products.title;
      var count=this.count;
      var price=this.products.price;
      var md_pic = this.md;
      var subtitle = this.products.subtitle;
      var old_price = this.products.old_price;
      var sold_count = this.products.sold_count;
      var uid = this.uid;
      var uname = this.uname;
      // console.log(title,subtitle,price,old_price,sold_count,md_pic,tid,uid,uname)
      var obj={title,subtitle,price,old_price,sold_count,md_pic,tid,uid,uname};
      var url="/collection/add";
       var obj=(Qs.stringify(obj));
        if (this.isLogin == false) {
          this.$router.push({path:"/home/users/login"
            });
          return;
        }
         if (this.isLogin == true) {
          // 
          if (this.iscollection == false) {
            // 
            this.$axios
              .post(
                url,
                obj
              )
              .then(res => {
                if(res.data.code==1){
                  this.iscollection = true;
                  Toast({
                    message:"",
                    duration:1500
                  })
                }else{
                  Toast({
                    message:",",
                    duration:1500
                  })
                }
              });
          } else {
            this.$axios
              .post(
                "/collection/delete",
                Qs.stringify({
                  tid: this.tid
                })
              )
              .then(res => {
                if(res.data.code==1){
                  Toast({
                    message:"",
                    duration:1500
                  })
                  this.iscollection = false;
                }
              });
          }
        }
          
    //          //vuex 
    //          this.$store.commit("increment",this.count);
    //         // this.$store.commit("clearcount")
    //       };
    //  }
   
   
    },
    goSub(){
      if(this.count<=1){return};
       this.count = this.count-1;
    },
    goAdd(){
      if(this.count>=99){return};
       this.count = this.count+1;
    },
    show_lgImg(){
      if(this.ifshow_lgImg===false){
        this.ifshow_lgImg = true;
      }else{
        this.ifshow_lgImg = false;
      }
    }
  },
  created() {
      (async function (self) {
        //1.
        var res = await self.$axios.get("/users/islogin")
        if (res.data.ok == 1) {
          self.isLogin = true;
          self.uname = res.data.uname;
          self.uid = res.data.uid;
        } else {
          self.isLogin = false;
        }
        //2.
        var res = await self.$axios.get(`/collection/iscollection?uname=${self.uname}&tid=${self.tid}`)
        if (res.data.code == 1) {
          self.iscollection = true;
        } else {
          self.iscollection = false;
        }
      })(this);
    this.$axios.get("details?tid=" + this.tid).then(res => {
        this.products = res.data.product;
        this.imgList  = res.data.pics;
        this.md = res.data.pics[0].md;
    });
  },
  components: {
    swiper
  }
};
</script>
